<template>
  <div class="EasyStarHeader">
      <el-row :gutter="24" justify="start" type="flex">
          <!-- <el-col :span="4">
           <div class="img" style="width:200px;"><img src="@/assets/images/jylogo.png" style="margin-top:0"></div>
          </el-col> -->
          <el-col :span="1"></el-col>
          <el-col :span="2">
            <div class="logo" v-if="typeof(this.$route.params.title)=='' || typeof(this.$route.params.title)=='undefined'">{{choosetitle}}</div>
            <div class="logo" v-if="typeof(this.$route.params.title)!='' || typeof(this.$route.params.title)!='undefined'">{{this.$route.params.title}}</div>
          </el-col>
          <el-col :span="2">
            <!-- <input type="file" name="file" multiple="multiple" /> -->
          </el-col>
          <el-col :offset="3" :span="6">
              <el-input
                placeholder="请输入查询内容"
                size="mini" title="请输入查询内容">
                 <el-button slot="append" icon="el-icon-search" size="mini" title="查询"></el-button>
              </el-input>
          </el-col>
          <el-col :span="8">
              <el-button @click="backToHome" size="mini" title="退出">退出</el-button>
          </el-col>
      </el-row>
  </div>
</template>
<script>
export default {
  name: 'EasyStarHeader',
  props: ['choosetitle'],
  data () {
    return {
      state3: '',
      imgUrl: require('@/assets/logo.png')
    }
  },
  methods: {
    backToHome () {
      this.$router.replace(
        {
          name: 'archivelist'
        }
      )
    }
  }
}
</script>
<style>
  .logo {
    position: absolute;
    font-size: 12px;
    font-weight:600;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-shadow:#fff 1px 0 0,#fff 0 1px 0,#fff -1px 0 0,#fff 0 -1px 0;
    -webkit-text-shadow:#fff 1px 0 0,#fff 0 1px 0,#fff -1px 0 0,#fff 0 -1px 0;
    -moz-text-shadow:#fff 1px 0 0,#fff 0 1px 0,#fff -1px 0 0,#fff 0 -1px 0;
    text-align: center;
    color: #2c3e50;
  }
</style>
<style scoped>
  i{
        line-height: 60px;
    }
  li {
      line-height: normal;
      padding: 7px;
    }
    .name {
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .addr {
      font-size: 12px;
      color: #b4b4b4;
    }
    .highlighted .addr {
      color: #ddd;
    }
    img{
        width: 40px;
        height: 40px;
        margin-top: 10px;
        border-radius: 25px;
    }
    .iconfont{
        font-size: 25px;
    }
    .el-button--small{
        padding: 0;
        background: #b3c0d1;
        border: 0;
    }
    .EasyStarHeader{
      height:100%;
      line-height:83px;
      color: #000!important;
    }
    .el-row{
      margin-bottom:0;
      height:100%;
    }
    .EasyStarHeader img{
      width:100%;
      height:auto ;
      margin-top:17px;
    }
    .EasyStarHeader .el-col-6{
      text-align:left;
    }
</style>



