// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill'
import Vue from 'vue'
import Axios from 'axios'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import Avue from '@smallwei/avue'
import '@smallwei/avue/lib/theme-chalk/index.css'
import EasyStarComponents from '@/components/EasyStarComponents.js'
import './assets/iconfont/iconfont.css'
import EasyStarAPI from '@/util/api/api.json'
import router from './router'
import App from './App'
// import { error } from 'util'

Vue.config.productionTip = false

Vue.prototype.api = process.env.NODE_ENV === 'production' ? '' : '/api'
Vue.prototype.axios = Axios
Vue.prototype.$axios = Axios
Vue.prototype.actionExt = '.do'
Vue.prototype.easyStarAPI = EasyStarAPI
Axios.defaults.withCredentials = true
// http响应拦截器
// Axios.interceptors.response.use(response => {
//   return response
// }, error => {
//   switch (error.response.status) {
//     case 401:
//       // 返回 401 清除token信息并跳转到登录页面
//       router.replace({
//         path: 'login'
//       })
//   }
//   return Promise.reject(error.response.data)
// })
Vue.use(EasyStarComponents)
Vue.use(ElementUI)
Vue.use(Avue)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  router,
  components: { App },
  template: '<App/>'
})



