<template>
  <div id="easystarmain" style="height: 100%;">
    <el-container style="height: 100%;">
      <!-- <el-header>
        <easy-star-header :choosetitle="title"></easy-star-header>
      </el-header>
      <el-main>
        <el-container style="height: 100%;">
          <el-aside width="200px">
            <easy-star-left ref="navleft"></easy-star-left>
          </el-aside>
          <el-main>
            <router-view ref="docMain"/>
          </el-main>
        </el-container>
      </el-main> -->
      <el-aside width="200px">
        <easy-star-left ref="navleft"></easy-star-left>
      </el-aside>
      <el-container>
        <el-header class="easystarmain_header">
          <easy-star-header :choosetitle="title"></easy-star-header>
        </el-header>
        <el-main>
          <router-view ref="docMain"/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'EasyStarMain',
  data () {
    return {
      title: '',
      classId: '',
      classType: ''
    }
  },
  created: function () {
    if (sessionStorage.getItem('classId') !== 'undefined' || sessionStorage.getItem('classId') !== 'undefined') {
      this.classId = sessionStorage.getItem('classId')
      this.classType = sessionStorage.getItem('classType')
      this.title = sessionStorage.getItem('title')
    } else {
      this.$router.push({
        name: 'archiveHome'
      })
    }
  },
  mounted: function () {
    this.classId = this.classId
    this.classType = this.classType
    this.title = this.title
    this.$refs['navleft'].getMenuData(this.classId, this.classType)
    this.$nextTick(function () {
      this.$router.replace({
        name: 'archiveHome'
      })
    })
  },
  methods: {
    windowResize: function () {
      if (this.$refs['docMain'] && this.$refs['docMain'].windowResize) {
        this.$refs['docMain'].windowResize()
      }
    }
  }
}
</script>

<style>
.easystarmain_header.el-header{
  height: 94px!important;
}
/* .el-header {
  background-color: #e3eef3;
  color: #9d9fa4;
  text-align: center;
  z-index: 10;
  padding-left: 0px;
  box-shadow: 0px 4px 5px #1679FA;
} */
.el-header {
  background-color: #2293FB;
  color: #333;
  text-align: center;
  /* line-height: 60px; */
  z-index: 10;
  box-shadow: 0 4px 5px #1679FA
}
.el-footer {
  background-color: #409EFF;
  color: #333;
  text-align: center;
  line-height: 60px;
}
/* .el-aside {
  background-color: #e3eef3;
  color: #9d9fa4;
  text-align: left;
  line-height: 100%;
  height: 100%;
} */
.el-aside {
  /* background-color: rgb(54, 145, 236); */
  background-color: #2293FB;
  color: #333;
  text-align: left;
  line-height: 100%;
  height: 100%;
}
.el-main {
  background-color: rgb(255, 255, 255);
  color: #333;
  width: 100%;
  height: 100%;
  padding: 0;
}
/* .el-main {
  background-color: #f4f9fa;
  color: #9d9fa4;
  width: 100%;
  height: 100%;
  padding: 0;
  margin-top: 2px
} */
</style>



